.login{
   height: 100vh;
   width: 100%;
   background: 
   linear-gradient(
         rgba(0, 0, 0, 0.4), 
         rgba(0, 0, 0, 0.4)
   ),url('/images/background.jpg') no-repeat center
}

.container{
   color: white;
   height: 100%;
}

.formContainer{
   text-align: center;
   display: flex;
   flex-direction: column;
   padding-top: 30vh;
   width: 500px;
   margin-right: auto;
   margin-left: auto;
}

.username{
   margin-bottom: 10px;
   flex: 1;
   color: white;
   width: 60%;
   padding: 8px 0;
   padding-left: 5px;
   font-size: 15px;
   border-radius: 5px;
   border: 2px solid hsla(0,0%,100%,.8)!important;
   background-color: transparent !important;
}

.password{
   margin-bottom: 10px;
   flex:1;
   color: white;
   border-radius: 5px;
   width: 60%;
   padding: 8px 0;
   padding-left: 5px;
   font-size: 15px;
   border: 2px solid hsla(0,0%,100%,.8)!important;
   background-color: transparent !important;
}

.submit{
   color: white;
   background: #e6b800;
   border: none;
   padding: 10px;
   border-radius: 5px;
   margin-top: 10px;
   width: 60%;
   cursor: pointer
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 0.7; /* Firefox */
}
