.MerchantContainer{
   display: flex;
   height: 100vh;
}

.textField{
   margin-left: 10px;
   color:white;
}

.navcontent{
   height: 100%;
   color: rgb(175, 124, 30);
   flex: 1;
   text-align: center;
   background: #525252;
}

.bodycontent{
   height: 100vh;
   flex: 4;
   /* background: #525252;
   color:white; */
   text-align: center
   
}

.topnav{
   display:flex;
  
}

.merchantbody{

}



.search{
   flex:1;
   text-align: left;
   margin-left: 30px;
}

.logout{
   flex:3;
   text-align: right;
   margin-right: 10px;
}

.nav{
   list-style: none;
   margin: 10px 0;
}

.bodycontainer{
   display: flex;
   flex-direction: column;
}

.BurgerMenu{
   display:none;
}

@media only screen and (max-width: 1211px) {
   
   .search{
      flex:3;
      text-align: left;
      margin-left: 0;
   }

   .textField{
      margin-top: 26px !important  ;
      color:white;
   }

   .burger{
      flex:1
   }

   h3{
      font-size: 14px;
   }

   .navcontent{
      display: none;
   }

   .BurgerMenu{
      display: inline;
      position: absolute;
   }

   .logout{
      display: none;
   }

}

@media only screen and (min-width: 1212px) {
   .burger{
      display: none;
   }
   .search{
      flex:3;
      text-align: left;
      margin-left: 30px;
   }
}
